}
.projects-wrapper{
    justify-content: center;
    max-width: 75%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr; 
    align-items: center;
    grid-gap: 16px;
    padding: 0 20px;
}


.h2{
    font-size: 30px;
}

.projects-card{

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    height: auto;
    padding: 0 10px;
    border-bottom: #D9D9D9 4px groove;
    color: white;
    overflow: hidden;
}

.projects-card h2{
