    text-align: center;
    color: #B0A8B9;
}

.video{
    
    width: 100%;
    height:300px;
    object-fit: cover;
    border-radius: 10px; 
    overflow: hidden;
  

}

.image{

    width: 100%;
    height:300px;
    object-fit: cover;
    border-radius: 10px; 
    overflow: hidden;
  

}

